.container {
	width: 35%;
	background-image: url("../Event/images/autumn-min.jpg");
	background-position: left;
	background-size: cover;
	position: relative;
}
.transBack {
	background-image: linear-gradient(
		rgba(255, 255, 255, 0.7),
		rgba(255, 255, 255, 0)
	);
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 1;
}

@media (max-width: 768px) {
	.container {
		width: 100%;
	}
}
